export var sleep = function (ms) {
    return new Promise(function (resolve) {
        setTimeout(resolve, ms);
    });
};
export var isTokenExpired = function (token) {
    // console.log(token);
    if (!token)
        return true;
    if (token === 'undefined')
        return true;
    console.log('checkToken after if', Math.floor(new Date().getTime() / 1000) >=
        JSON.parse(atob(token.split('.')[1])).exp);
    var expiry = JSON.parse(atob(token.split('.')[1])).exp;
    return Math.floor(new Date().getTime() / 1000) >= expiry;
};
